<template>
  <div class="container">
    <article
      v-html="contentHtml"
    ></article>
    <Errormes ref="Errormes"></Errormes>
  </div>
</template>
<script>

// 域名/article/publishLocationCode 如: (202001)/publishChannelCodes 如:(6)
import Errormes from '../../components/Errormes/index.vue'
export default {
  name: 'general',
  components: {
    Errormes
  },
  data () {
    return {
      contentHtml: ''
    }
  },
  mounted () {
    this.$apis.getArticlePublispList({
      publishLocationCode: 202001,
      publishChannelCodes: 6
    }).then(res => {
      if (res.code === 200) {
        const result = res && res.result
        document.title = result.articleTitle
        this.contentHtml = result.contentHtml
      } else {
        this.$refs.Errormes.ErrormesFun(res.message)
      }
    })
  }
}
</script>

<style lang="less" scoped>
.container {
  color: #666;
  padding: 30px;
  font-size: 28px;
  line-height: 44px;
}
</style>
